import React from "react"
import styled, { keyframes } from "styled-components"
import { Link } from "gatsby"
import { TitleBold, Paragraph } from "../components/textstyling"

const displace = keyframes`
    0%{background-position:0% 50%;}
    50%{background-position:100% 50%;}
    100%{background-position:0% 50%;}
`

const ProjectCardWrapper = styled(Link)`
  border-radius: 9px;
  background: ${props =>
    props.gradientBackground ||
    "-webkit-linear-gradient(45deg, #ff278b 0%, #fca734 55%)"};
  height: 20vw;
  background-size: 250% 250%;
  cursor: pointer;
  :hover {
    animation: ${displace} 4s linear infinite;
  }

  @media only screen and (max-width: 1000px) {
    height: 25vw;
  }

  @media only screen and (max-width: 750px) {
    height: 40vw;
  }

  @media only screen and (max-width: 450px) {
    height: 45vw;
  }
`

const ProjectCardTitle = styled(TitleBold)`
  color: ${props => props.theme.whitecolor};
  font-size: 2.2vw;
  text-align: right;
  margin-right: 1.5vw;
  margin-top: 14vw;
  bottom: 0;

  @media only screen and (max-width: 1000px) {
    margin-top: 17vw;
    font-size: 3vw;
  }

  @media only screen and (max-width: 750px) {
    margin-top: 25vw;
    font-size: 2.3em;
    margin-right: 3vw;
  }

  @media only screen and (max-width: 450px) {
    font-size: 1.5em;
    margin-top: 28vw;
  }

  @media only screen and (max-width: 322px) {
    font-size: 1.2em;
  }
`

const ProjectCardText = styled(Paragraph)`
  color: ${props => props.theme.whitecolor};
  font-size: 1.1vw;
  text-align: right;
  margin-right: 1.5vw;
  margin-top: 0.4em;

  @media only screen and (max-width: 1025px) {
    font-size: 1.4vw;
    margin-top: 0.3em;
  }

  @media only screen and (max-width: 750px) {
    margin-right: 3vw;
    font-size: 0.8em;
  }

  @media only screen and (max-width: 450px) {
    margin-right: 3vw;
    font-size: 0.8em;
    margin-top: 2.5vw;
  }

  @media only screen and (max-width: 322px) {
    font-size: 0.65em;
  }
`

export const ProjectCard = props => (
  <ProjectCardWrapper
    gradientBackground={props.gradientBackground}
    to={props.cardlink}
  >
    <ProjectCardTitle>{props.projectcardtitle}</ProjectCardTitle>
    <ProjectCardText>{props.projectcardtext}</ProjectCardText>
  </ProjectCardWrapper>
)
